  <div>
    <!-- <v-form @submit.prevent="submit" ref="form"> -->
    <h3 class="d-flex align-center mb-3">
      Financial Period
      <v-spacer></v-spacer>
      <template v-if="resource.data.id">
        <input
          type="hidden"
          name="metadata[statement][id]"
          :value="resource.data.id"
        />
        <v-switch
          :label="`${edit ? 'Edit' : 'View'}`"
          class="mt-0"
          color="primary"
          hide-details
          v-model="edit"
        ></v-switch>
      </template>
      <template v-else>
        <span class="grey--text text--darken-2">Add</span>
      </template>
    </h3>
    <input type="hidden" name="metadata[setMethod]" :value="resource.data.setMethod" />
    <validation-provider
      vid="description"
      :name="trans('Description')"
      v-slot="{ errors }"
      v-if="edit"
    >
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <!-- <v-text-field
            v-model="formattedDate"
            label="Period Date"
            prepend-icon="mdi-calendar"
            readonly
            name="metadata[statement][metadataStatements][period]"
            v-bind="attrs"
            v-on="on"
          ></v-text-field> -->
          <v-text-field
            dense
            label="Description"
            name="metadata[statement][metadataStatements][period]"
            outlined
            v-model="resource.data.period"
            hide-details
          ></v-text-field>
        </template>
        <!-- <v-date-picker
          v-model="unFormattedDate"
          no-title
          scrollable
        >
        </v-date-picker> -->
      </v-menu>
      <!-- <v-text-field
        dense
        label="Description"
        name="metadata[statement][metadataStatements][period]"
        outlined
        v-model="resource.data.period"
        hide-details
      ></v-text-field> -->
      <!-- <input
        type="hidden"
        name="metadata[statement][metadataSheets][period]"
        :value="resource.data.period"
      /> -->
    </validation-provider>

    <h4 class="mb-3 primary--text" v-else v-text="resource.data.period">
      Financial Period
    </h4>

    <v-divider class="my-10"></v-divider>

    <h3>Financial Statement</h3>

    <div
      v-for="(item, i) in Object.keys(resource.data.metadataStatements)"
      :key="i + 'a'"
    >
      <template v-if="item === 'Net Operating Profit/(Loss)'">
        <v-row>
          <v-col
            cols="6"
            v-text="trans(item)"
            class="text-right font-weight-bold"
          >
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="text-right dt-text-field"
              :class="
                resource.data.metadataStatements[item] > 0
                  ? 'text-green'
                  : 'text-red'
              "
              :color="
                resource.data.metadataStatements[item] > 0 ? 'green' : 'red'
              "
              dense
